}
#toggel1 {
width: 100%;
height: 70px;
background: linear-gradient(121.34deg, rgba(228, 228, 228, 0.2) 14.34%, rgba(187, 187, 187, 0.2) 71.71%);
box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(20px);
border-radius: 40px;
display: flex;
justify-content: space-around;
align-items: center;
cursor: pointer;
}
#content {
margin-top: 10px;
width: 100%;
height: 150px;
background: #F1F1F1;
border-radius: 40px;
display: flex;
justify-content: center;
align-items: center ;
transition: all ease-in-out 0.3s;
}
#con {
 
    display: none;
  
    }
#content div {
    width: 90%;
}
#content div h4 {
    font-family: 'Roboto';
font-style: normal;
font-weight: 500;
font-size: 20px;
line-height: 23px;

color: #686868;

}
